.Statistika {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 989px;
    margin: 0 auto;
    padding-top: 150px;
    padding-bottom: 144px;

    &-wrap {
        width: 100%;
        max-width: 250px;
        text-align: center;

    }

    &-title {
        font-family: 'Allerta Stencil';
        font-style: normal;
        font-weight: 400;
        font-size: 60px;
        line-height: 117.2%;
        color: #F4013D;
        text-align: center;
    }

    &-text {
        font-family: 'Laviossa RUS';
        font-style: normal;
        font-weight: 500;
        font-size: 22px;
        line-height: 117.2%;
        text-align: center;
        color: #FFFFFF;
    }
}